import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonService } from 'src/app/service/common.service';
import { UnitService } from 'src/app/service/unit.service';


@Component({
  selector: 'app-unit-add',
  templateUrl: './unit-add.component.html'
})
export class UnitAddComponent implements OnInit {

  PageTitle = "Unit"

  loadingState = false;
  addForm: FormGroup;

  showLoader = false;
  formErrors = {
    unit_name: null,
    unit_code: null,
    apierror: null,
  };
  isEditing = false;
  editId = null;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private unitservice: UnitService,
    private commonservice: CommonService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
    }

    this.addForm = this.fb.group({
      unit_name: [null, Validators.compose([Validators.required])],
      unit_code: [null, Validators.compose([Validators.required])],

    });
  }

  ngOnInit() {
    if (this.isEditing) {
      this.getEditObject()
    }
  }






  getEditObject() {
    this.unitservice.getUnitById(this.editId).subscribe((response) => {
      if (response.success) {
        this.addForm.patchValue(response.data);
      } else {
        this.router.navigateByUrl('/manage/unit');
      }
    });
  }

  submitForm(formData: any): void {
    this.removeError();
    if (formData.valid) {
      this.showLoader = true;
      const data = new FormData();
      data.append('unit_name', formData.value.unit_name);
      data.append('unit_code', formData.value.unit_code);

      if (this.isEditing) {
        this.unitservice.editUnit(this.editId, data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/unit');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('unit_name')) {
                this.formErrors['unit_name'] = obj['unit_name'];
              }
              else if (obj.hasOwnProperty('unit_code')) {
                this.formErrors['unit_code'] = obj['unit_code'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      } else {
        this.unitservice.addUnit(data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/unit');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('unit_name')) {
                this.formErrors['unit_name'] = obj['unit_name'];
              }
              else if (obj.hasOwnProperty('unit_code')) {
                this.formErrors['unit_code'] = obj['unit_code'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      }
    }
  }
  removeError(){
    this.formErrors = {
      unit_name:null,
      unit_code: null,
      apierror: null,
    };
  }
}
